.featured{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.featuredItem{
    flex: 1px;
    margin: 0px 20px;
    padding: 30px;
    cursor: pointer;
    background-color: rgb(247, 232, 211);
    -webkit-box-shadow: 2px 7px 10px 0px rgba(0,0,0,0.59);
-moz-box-shadow: 2px 7px 10px 0px rgba(0,0,0,0.59);
box-shadow: 2px 7px 10px 0px rgba(0,0,0,0.59);
}
.featuredTitle{
    font-size: 20px;
}
.featuredMoneyContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}
.featuredMoney{
    font-size: 30px;
    font-weight: 600;
}
.featuredMoneyRate{
    display: flex;
    align-items: center;
    margin-left: 20px;
}
.featuredIcon{
    font-size: 14px;
    margin-left: 5px;
    color: green;
}
.featuredIcon.negative{
    color: red;
}
.featuredSub{
    font-size: 15px;
    color: grey;
}