.topbar{
    width: 100%;
    height: 60px;
    background-color:#585858;
    position: sticky;
    top: 0;
    z-index: 999;
}
.icon{
    color: aliceblue;  
}
.topLeft{
    display: flex;
    align-items: center;
}
.topbarWrapper{
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo{
    font-weight: bold;
    font-size: 20px;
    /* font-family: "Lucida Console", "Courier New", monospace; */
    font-family: 'Bahnschrift SemiBold';
    color: rgb(255, 255, 255);
    cursor: pointer;
    margin-left:5px ;
}
.topRight{
    display: flex;
    align-items: center;
}
.topbarIconContainer{
    position: relative;
    cursor: pointer;
    margin-right:10px;
    color: rgb(124, 121, 121);

}
.topIconBadge{
    position: absolute;
    width: 15px;
    height: 15px;
    top: -5px;
    right: 0px;
    background-color: red;
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}
.topAvatar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
}