
  .table{
    flex: 4;
  }
  th, td {
    border-bottom: 1px solid #ddd;
  }
  td{
    align-items: center;
    padding: 15px;
    margin: auto;
  }
  tbody{
    align-items: center;
    text-align: center;
  }
  Table{
    width: 100%;
  }
  .create{
    float: right;
    margin-bottom: 40px;
    margin-top: -30px;
    margin-right: -30px;
  }
  .productCreate{
    border: none;
    border-radius: 6px;
    padding:  8px;
    background-color: #1f9b01;
    color: white;
    cursor: pointer;
    margin-right: 20px;
}
  .productListEdit{
      border: none;
      border-radius: 10px;
      padding: 5px 10px;
      background-color: #298ce7;
      color: white;
      cursor: pointer;
      margin-right: 20px;
  }
  .imageName{
    align-items: center;
    text-align: center;
  }
  .productListItem{
    display: flex;
    align-items: center;
}
  .productListImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
  }
.productButtons{
    align-items: center;
    text-align: center;
}
  
